.app {
  width: 100%;
  height: 100vh;
  background: #ddd;
}

.navigateMain {
  user-select: none;
  width: 100%;
  padding: 10px;
  background: #222;
  box-sizing: border-box;
  display: flex;
}

.navigateLink {
  color: #222;
  text-decoration: none;
  background: #ddd;
  padding: 10px;
  margin: 10px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.navigateLink, .navigateLink::after, .navigateLink::before {
    user-drag: none;
    -webkit-user-select: none;
    -webkit-user-drag: none;
    -webkit-app-region: no-drag;
}
